@mixin crystal {
  @include crystal-common;

  .wcClassic {
    border: 0;
    -webkit-border-radius: 5px;
    -moz-border-radius: 5px;
    border-radius: 5px;
    box-shadow: 0 0 10px rgba(0,0,0,0.08);
    overflow: hidden;
    background: #f7f7f7;

    .wcTitle {
      padding: 15px; font-size: 150%; color: #2a292a; background: #e0e0e0;
    }

    .wcLoadingContainer, .wcAuthContainer {
      padding: 0;
    }

    // desktop:
    &.wcDesktop {
      .wcBody {
        // messages area:
        .wcMessagesArea {
          .wcTabsContainer {
            background-color: #ededed;

            .wcTabs {
              .wcTab {
                &.wcCurrent {
                  border-bottom: 3px solid transparent;
                  background: #f7f7f7;
                  border-radius: 5px 5px 0 0;

                  &:first-child {
                    border-top-left-radius: 0;
                  }
                }

                .wcName {
                  font-size: 16px;
                  padding-top: 4px;
                  padding-bottom: 4px;
                }
              }
            }

            .wcTabsNav .wcLeft {
              background-image: url("../../gfx/themes/crystal/gfx/arrow-left.svg");
            }
            .wcTabsNav .wcRight {
              background-image: url("../../gfx/themes/crystal/gfx/arrow-right.svg");
            }
          }

          .wcCustomizations {
            padding: 0 2% 2% 2%;
            background-color: #f7f7f7;
          }

          .wcGrid .wcGridChannelContainer {
            .wcGridChannel {
              border-color: #e4e4e4;
              border-radius: 5px 5px 0 0;

              .wcHeader {
                background: #e0e0e0;
                border-radius: 5px 5px 0 0;
                color: #2a292a;
              }
            }

            &.wcFocused .wcGridChannel {
              border: 1px solid rgba(207, 207, 207, 0.84);
              box-shadow: 0 0 7px rgba(207, 207, 207, 0.94);
              .wcChannel {
                .wcInput {
                  border: 1px solid rgba(207, 207, 207, 0.54);
                }
              }
            }
          }
        }

        // browser area:
        &.wcBrowserAreaRight {
          .wcMessagesArea {
            padding-left: 0;
            padding-right: 0;
          }
        }

        &.wcBrowserAreaLeft {
          .wcMessagesArea {
            padding-left: 0;
            padding-right: 0;
          }
          .wcBrowserArea {
            padding-right: 2%;
            padding-left: 1%;
          }
        }

        .wcBrowserArea {
          background-color: #ededed;
        }
      }
    }
  }

  .wcMobile, .wcSidebar.wcMobile {
    .wcContent {
      padding-left: 0;
      padding-right: 0;
    }
    .wcTabs {
      background-color: #ededed;
      .wcTab {
        padding-bottom: 6px;
        padding-top: 6px;

        .wcName {
          font-size: 16px;
        }
        border-bottom: 0;
        &.wcCurrent {
          border-bottom: 0;
          background-color: #f9fafb;
          border-radius: 5px 5px 0 0;
        }

        .wcRedCounter {
          width: 22px;
          height: 22px;
          font-size: 1em;
        }
      }

      .wcTabCustomize {
        background-image: url(../../gfx/themes/crystal/gfx/custom.svg);
      }
    }
    &.wcSizeXXs {
      .wcTabs.wcTabsCompact {
       .wcTab {

          &.wcTabRecent {
            background-image: url(../../gfx/themes/crystal/gfx/recent-chats-inactive.svg);
          }

          &.wcTabChats {
            background-image: url(../../gfx/themes/crystal/gfx/chats.svg);
          }

          &.wcTabUsers {
            background-image: url(../../gfx/themes/crystal/gfx/user.svg);
          }

          &.wcTabChannels {
            background-image: url(../../gfx/themes/crystal/gfx/group.svg);
          }
       }
      }
    }

    .wcTabContent {
      .wcChannel {
        .wcChannelIntro {
          background-color: transparent;
          margin-top: 0;
          margin-bottom: 1%;
          padding: 0 0 2% 0;
        }
      }
      .wcChannelEntry {
        padding: 5px 10px 5px 10px;

        &:focus, &:active, &:hover {
          background-color: rgba(255,255,255,0.35);
        }

        &.wcUnLockedChannel .wcName {
          background-image: url("../../gfx/themes/crystal/gfx/lock-semi.svg");
        }
        &.wcLockedChannel .wcName {
          background-image: url("../../gfx/themes/crystal/gfx/lock-full.svg");
        }
      }

      .wcBrowser {
        padding-top: 0;
        background-color: #f7f7f7;

        .wcChannels {
          .wcChannelTrigger {
            &:hover {
              background-color: rgba(255,255,255,0.35);
            }
          }
        }
      }

      .wcRecentChats {
        .wcHeader {
          font-size: 1.1em;
          color: #444;
          padding-left: 10px
        }

        .wcRecent {
          padding: 5px 10px 5px 10px;

          .wcRight .wcHead {
            margin-bottom: 0;
          }
        }
      }

      .wcCustomizations {
        padding-left: 2%;
        padding-right: 2%;
      }

    }
  }

  .wcSidebar .wcColumn {
    > * {
      border-color: #e4e4e4;
    }

    &.wcFocused>:first-child {
      border-top: none;
      > span {
        font-weight: bold;
      }
    }

    .wcHeader {
      height: 2.6em;
      background: #e0e0e0;
      border-radius: 5px 5px 0 0;
      color: #2a292a;

      .wcButtons {
        .wcMinimize {
          background-image: url("../../gfx/themes/crystal/gfx/minus.svg");
        }
        .wcMaximize {
          background-image: url("../../gfx/themes/crystal/gfx/plus.svg");
        }
        .wcClose {
          background-image: url("../../gfx/themes/crystal/gfx/x.svg");
        }
      }
    }

    .wcContent {
      background-color: #f7f7f7;
      &.wcBrowserContent .wcCustomizations {
        background-color: #ededed;
      }

      .wcChannel {
        .wcChannelIntro {
          background-color: transparent;
          padding-left: 0;
          padding-right: 0;
        }
      }
    }

    .wcRecentTrigger {

      span {
        top: 10px;
      }
    }
  }

  // common:
  .wcCustomizations {
    a, a:hover {
      color: #686868; outline: none;
    }
    a.wcCustomizeButton:before {
      background: url(../../gfx/themes/crystal/gfx/custom.svg) center center no-repeat;
    }
  }

  .wcCounter {
    font-size: 10px;
  }

  .wcBrowser {
    .wcChannels {
      .wcLabel {
        font-weight: bold;
      }
      .wcChannelTrigger {
        color: #151618;

        &.wcUnLockedChannel .wcDetails .wcName {
          background-image: url("../../gfx/themes/crystal/gfx/lock-semi.svg");
        }
        &.wcLockedChannel .wcDetails .wcName {
          background-image: url("../../gfx/themes/crystal/gfx/lock-full.svg");
        }

        &:hover {
          background-color: rgba(255,255,255,0.35);
          border-radius: 5px;
        }
      }

      .wcLabel {
        font-size: 1.1em;
        color: #444;
      }
    }

    background-color: #ededed;
    color: #151618;

    .wcCounter {
      border: 0;
      border-top: 1px solid #e9eef0;
    }

    .wcFooter {
      border-top: none;
      padding: 5px 5px 5px 10px;

      .wcSearch {
        color: #2a292a; font-size: 20px;
        background-color: #fafafa;
        border-radius: 5px;
        background-position: 10px center;
        background-image: url("../../gfx/themes/crystal/gfx/search.svg");

        input {
          background-color: #fafafa;
          margin-left: 30px;
          &:focus {
            padding-left: 2px;
          }
        }
      }
    }
  }

  .wcChannel {
    .wcChannelIntro {
      border: none;
      padding: 10px;
      margin-top: 0;
      margin-bottom: 0;
      .wcAvatar {
        border-radius: 5px;
        max-height: 60px;
      }
    }

    .wcChannelData .wcMessages {
      color: #111;
      background: #f7f7f7;
      margin-bottom: 2%;

      .wcMessage {
        padding: 5px 2%;
        border-bottom: 1px solid #ededed;
        margin: 0;

        .wcRowBody .wcContent {
          background-color: transparent;
        }

        a {
          color: #6272d0;
          text-decoration: none;
          &:hover {
	        text-decoration: underline;
          }
        }

        .wcUser {
          line-height: 22px;
          color: #7f7f7f;
          text-decoration: none;
          font-weight: bold;
        }

        .wcTime {
          &, & * {
            color: #aeaeae; line-height: 22px;
          }
        }

        .wcRowBody .wcContent {
          .wcActionsButton {
            border-radius: 5px;
          }

          .wcQuote {
            border-left-color: #525252;
            background-color: #f3f3f3;
            border-radius: 0 5px 0 0;
          }
        }

        &.wcCurrentUser {
          background: #f3f3f3;

          .wcRowBody .wcContent .wcQuote {
            border-radius: 0 5px 0 0;
            background-color: #ededed;
          }
          .wcRowHead .wcUser {
            color: #444;
          }
        }
      }
    }

    .wcCounter {
      padding: 0 2% 1% 2%;
    }

    .wcChannelInput {
      background-color: #f7f7f7;
      padding: 0 2% 2% 2%;

      .wcCurrentUserName {
        padding-bottom: 7px;
        font-weight: bold;
        color: #444;
      }

      .wcReplyTo {
        background-color: #ededed;
        .wcDeleteButton {
          background-image: url("../../gfx/themes/crystal/gfx/x.svg");
        }
      }

      .wcInputs {
        .wcInputButtons {
          .wcInputButton {
            border-radius: 0;
            border: none;
            box-shadow: none;
            height: 40px;
            background: #ffffff;
            display: inline-block;
            outline: none;

            -webkit-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
            -o-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
            transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);


            &:hover, &:hover > * {
              cursor: pointer;
            }

            &.wcImageAttachment {
              background: transparent url('../../gfx/themes/crystal/gfx/camera.svg') center center no-repeat;
            }

            &.wcFileAttachment {
              background: transparent url('../../gfx/themes/crystal/gfx/attach.svg') center center no-repeat;
              cursor: pointer;
            }

            &.wcEmoticon {
              background: transparent url('../../gfx/themes/crystal/gfx/emoticon.svg') center center no-repeat;
            }

            &.wcSoundAttachmentStart {
              background: transparent url(../../gfx/themes/crystal/gfx/mic.svg) center center no-repeat;
            }

            &.wcSoundAttachmentStop {
              background: transparent url(../../gfx/icons/stop-button.svg) center center no-repeat;
            }

            &:focus, &:active, &:hover {
              background-color: #fff;
              border-radius: 5px;
              border: none;
            }

          }

          .wcInputButtonSupplementary {
            background-color: transparent;
          }
        }

        div.wcInput {
          line-height: 30px; box-shadow: none;
          background-color: #fff; border-radius: 5px; margin: 0;
          padding: 10px 5px 10px 15px; border: 1px solid transparent;
        }

        div.wcInput:focus {
          background-color: #fff; border: 1px solid transparent;
        }

        div.wcInput {
          min-height: 2.5rem; font-size: 20px;
          * {
            font-size: 20px;
          }
        }
      }

      .wcAttachments {
        .wcAttachment {
          .wcDelete {
            border-color: #525252;
            background: transparent url("../../gfx/themes/crystal/gfx/x.svg") center center no-repeat;
            &:hover {
              background-color: #e3e3e3;
            }
          }
        }
      }
    }
  }

  .wcRecentTrigger {
    &:hover {
      background-color: rgba(9, 193, 121, 0.15);
    }
    background-image: url("../../gfx/themes/crystal/gfx/recent-chats-inactive.svg");
    &.wcOpen {
      background-image: url("../../gfx/themes/crystal/gfx/recent-chats.svg");
    }

    span {
      font-size: 11px;
      font-weight: bold;
    }
  }
}

@mixin crystal-global {

  [role=tooltip].wcPopup-content.wcCrystalTheme-content,
  [role=dialog].wcPopup-content.wcCrystalTheme-content {
    @include crystal-common;

    border-color: #e9eef0;
    box-shadow: 2px 2px 15px rgba(60,60,60,0.1);
    border-radius: 5px;
    overflow: hidden;

    > .wcBody {
      background-color: #FFF !important;
      padding-top: 20px;
      padding-bottom: 20px;
    }

    > .wcHeader, > .wcFooter {
      background: #ececec !important;
      border: none;
    }
    > .wcHeader {
      color: #2a292a;
      h5 {
        font-size: 1.1em;
        font-weight: bold;
      }
      .wcClose {
        background: transparent url("../../gfx/themes/crystal/gfx/x.svg") center center no-repeat;
        background-size: 100%;
      }
    }

    .wcRecentChats {
      .wcHeader {
        font-size: 1.1em;
        font-weight: bold;
        color: #444;
      }
      .wcRecent {
        .wcRight .wcHead {
          margin-bottom: 0;
          .wcName {
            color: #444;
          }
        }
      }
    }
  }

  .wcActionsPopup-content.wcCrystalTheme-content {
    .wcActions {

      .wcAction {
        border-color: #525252;

        &:hover {
          background-color: #e3e3e3;
        }
      }

      .wcApprove {
        background: transparent url("../../gfx/themes/crystal/gfx/approve.svg") no-repeat 50%;
      }

      .wcDelete {
        background: transparent url("../../gfx/themes/crystal/gfx/x.svg") no-repeat 50%;
      }

      .wcEdit {
        background: transparent url("../../gfx/themes/crystal/gfx/edit.svg") no-repeat 50%;
      }

      .wcMute {
        background: transparent url("../../gfx/themes/crystal/gfx/mute.svg") no-repeat 50%;
      }

      .wcBan {
        background: transparent url("../../gfx/themes/crystal/gfx/ban.svg") no-repeat 50%;
      }

      .wcSpam {
        background: transparent url("../../gfx/themes/crystal/gfx/spam.svg") no-repeat 50%;
      }

      .wcReply {
        background: transparent url("../../gfx/themes/crystal/gfx/reply.svg") no-repeat 50%;
      }
    }
  }

  .wcEmoticonsPopup-content.wcCrystalTheme-content {
    .wcAddonsLibrary .wcCategories .wcCategoryButton {
      &.wcEmoticon {
        background-image: url(../../gfx/themes/crystal/gfx/emoticon.svg);
      }
      &.wcGIF {
        background-image: url(../../gfx/themes/crystal/gfx/gif.svg);
      }
    }

    .wcCategory.wcCategoryGIFs .wcTenor .wcTenorSearch {
      background-image: url(../../gfx/themes/crystal/gfx/search.svg);
      border-radius: 5px;
      input {
        background-color: #ffffff;
      }
    }
  }

  .wcInfoWindowPopup-content.wcCrystalTheme-content {
    background: #FFF !important;
    height: 120px;

    .wcContent .wcRight {
      .wcName {
        font-size: 1.3em;
        text-decoration: none;
      }
    }
  }
  .wcRecentEmpty-content.wcCrystalTheme-content {
    height: 100px;
    .wcRecentChats {
      display: flex;
      align-items: center;
    }
  }
}

@mixin crystal-common {
  --wc-font-color: #2a292a;
  --wc-link-color: #6272d0;

  &, * {
    font-family: 'Rajdhani', sans-serif;
    font-size: 13px;
    line-height: 22px;
    color: var(--wc-font-color);
  }

  a {
    color: var(--wc-link-color);
    box-shadow: none;
  }

  button.wcButton, button.wcButton:focus, button.wcButton:active,
  input[type="button"].wcButton, input[type="button"].wcButton:focus, input[type="button"].wcButton:active,
  input[type="submit"].wcButton, input[type="submit"].wcButton:focus, input[type="submit"].wcButton:active {
    border-radius: 5px; background-image: none; background-repeat: no-repeat; border: 0!important;
	color: #FFF !important; padding: 5px 20px !important; box-shadow: none; font-weight: normal !important; line-height: 20px !important;
	height: 2.5rem; outline: none; min-height: 40px !important;
	background: #2a292a !important;
	text-transform: none !important;
    font-size: 20px;

	-webkit-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
	-o-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
	transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);

    &:hover {
      background: #292952 !important;
      box-shadow: none !important; font-weight: normal !important;
    }

    &.wcButtonDanger {
      color: #fff !important;
      background: #dc3545 !important;
      border-color: #dc3545 !important;
      &:hover {
        background: #ff0000 !important;
      }
    }
  }



  input[type="text"].wcControl, input[type="password"].wcControl, textarea.wcControl {
    height: 40px; font-size: 15px; line-height: 40px; box-shadow: none;
	background-color: #e0e0e0; border-radius: 5px; margin: 0; outline: 0 none; padding: 4px;
	padding-left: 15px; border: 1px solid transparent;
  }

  input[type="text"]:focus.wcControl, input[type="password"]:focus.wcControl, textarea:focus.wcControl {
    background-color: #e0e0e0; padding-left: 15px; border: 1px solid transparent;
  }

  ::placeholder {
    color: #686868;
    font-family: 'Rajdhani', sans-serif;
  }

  input[type="button"].wcSubmit {
      border-radius: 5px; text-transform: uppercase !important; font-weight: bold !important; font-size: 20px; padding: 20px 20px !important; height: 2.5rem; margin-top: -1px;
  }


  progress, progress[role][aria-valuenow] {
    background: #b8bcc4 !important;
  }

  progress::-webkit-progress-bar {
    background: #b8bcc4;
  }
  /* Progress bar - foreground */
  progress {
    color: #6272d0;
  }

  progress::-moz-progress-bar {
    background: #6272d0;
  }

  progress::-webkit-progress-value {
    background: #6272d0;
  }

  progress[aria-valuenow]:before  {
    background: #6272d0;
  }

  @media screen and (-webkit-min-device-pixel-ratio:0) {
    * {
      select,
      textarea,
      button,
      input {
        font-size: 16px;
      }
    }
  }

  .wcErrorBox {
    border-radius: 5px;
    background-color: #ffe9e9;
    border: none;
  }

}