@mixin balloon {
  @include balloon-common;

  .wcClassic {
    border: 0;
    border-radius: 15px;
    box-shadow: 5px 5px 25px rgba(0,0,0,0.1);
    overflow: hidden;
    background: #f7f7f7;

    .wcTitle {
      padding: 15px; font-size: 150%; color: #d7ebfb; background: #2167a9;
    }

    .wcLoadingContainer, .wcAuthContainer {
      padding: 0;
    }

    // desktop:
    &.wcDesktop {
      .wcBody {
        // messages area:
        .wcMessagesArea {
          .wcTabsContainer {
            background-color: #3277bb;

            .wcTabs {
              .wcTab {
                border-top-left-radius: 5px;
                border-top-right-radius: 5px;
                &.wcCurrent {
                  border-bottom: 3px solid transparent;
                  background: #f7f7f7;

                  &:first-child {
                    border-top-left-radius: 0;
                  }
                  .wcName {
                    color: #151618;
                  }
                }

                .wcName {
                  color: #fff;
                  font-size: 16px;
                  padding-top: 4px;
                  padding-bottom: 4px;
                }
              }
            }

            .wcTabsNav .wcLeft {
              background-image: url("../../gfx/themes/balloon/gfx/arrow-left.svg");
            }
            .wcTabsNav .wcRight {
              background-image: url("../../gfx/themes/balloon/gfx/arrow-right.svg");
            }
          }

          .wcCustomizations {
            padding: 0 2% 2% 2%;
            background-color: #f7f7f7;
          }

          .wcGrid .wcGridChannelContainer {
            .wcGridChannel {
              border: none;

              .wcHeader {
                height: 3em;
                color: #d7ebfb; background: #2167a9;
                border-color: #2167a9;
                border-radius: 8px 8px 0 0;
                .wcName {
                  color: #fff;
                }
                .wcChannelClose:hover {
                  background-color: #2167a9;
                }
              }
              .wcChannel {
                border: 1px solid #e4e4e4;
                border-top: none;
              }
            }

            &.wcFocused .wcGridChannel {
              border: none;
              box-shadow: 0 0 7px rgba(33, 103, 169, 0.49);
              border-radius: 8px 8px 0 0;
              .wcChannel {
                border-color: #bfbebe;
                .wcInput {
                  border: 1px solid rgba(33, 103, 169, 0.10);
                }
              }
            }
          }
        }

        // browser area:
        &.wcBrowserAreaRight {
          .wcMessagesArea {
            padding-left: 0;
            padding-right: 0;
          }
        }

        &.wcBrowserAreaLeft {
          .wcMessagesArea {
            padding-left: 0;
            padding-right: 0;
          }
          .wcBrowserArea {
            padding-right: 2%;
            padding-left: 1%;
          }
        }

        .wcBrowserArea {
          background-color: #3277bb;

          .wcIcon {
            background-color: #ffffff;
          }
        }
      }
    }
  }

  .wcMobile, .wcSidebar.wcMobile {
    .wcContent {
      padding-left: 0;
      padding-right: 0;
    }
    .wcTabs {
      background-color: #3277bb;
      .wcTab {
        padding-bottom: 6px;
        padding-top: 6px;
        border-top-left-radius: 5px;
        border-top-right-radius: 5px;

        .wcName {
          font-size: 16px;
          color: #fff;
        }
        border-bottom: 0;
        &.wcCurrent {
          border-bottom: 0;
          background-color: #f9fafb;
          .wcName {
            color: #151618;
          }
        }

        .wcRedCounter {
          width: 22px;
          height: 22px;
          font-size: 1em;
        }
      }

      .wcTabCustomize {
        background-image: url(../../gfx/themes/balloon/gfx/custom-white.svg);
      }
    }
    &.wcSizeXXs {
      .wcTabs.wcTabsCompact {
       .wcTab {

          &.wcTabRecent {
            background-image: url(../../gfx/themes/balloon/gfx/recent-chats-inactive.svg);
          }

          &.wcTabChats {
            background-image: url(../../gfx/themes/balloon/gfx/chats.svg);
          }

          &.wcTabUsers {
            background-image: url(../../gfx/themes/balloon/gfx/user.svg);
          }

          &.wcTabChannels {
            background-image: url(../../gfx/themes/balloon/gfx/group.svg);
          }
       }
      }
    }

    .wcTabContent {
      .wcChannel {
        .wcChannelIntro {
          background-color: transparent;
          margin-top: 0;
          margin-bottom: 1%;
          padding: 0 0 2% 0;
        }
      }
      .wcChannelEntry {
        padding: 5px 10px 5px 10px;

        &:focus, &:active, &:hover {
          background-color: rgba(255,255,255,0.35);
        }

        &.wcUnLockedChannel .wcName {
          background-image: url("../../gfx/themes/balloon/gfx/lock-semi.svg");
        }
        &.wcLockedChannel .wcName {
          background-image: url("../../gfx/themes/balloon/gfx/lock-full.svg");
        }
      }

      .wcBrowser {
        padding-top: 0;
        background-color: #f7f7f7;

        .wcChannels {
          .wcChannelTrigger {
            .wcName {
              color: #151618;
            }
            &:hover {
              background-color: rgba(255,255,255,0.35);

              .wcDetails {
                button.wcStreamStartButton.wcStreamVideo {
                  background-image: url(../../gfx/icons/video-calls/video.svg);
                }
              }
            }
          }
        }
      }

      .wcRecentChats {
        .wcHeader {
          font-size: 1.1em;
          color: #444;
          padding-left: 10px
        }

        .wcRecent {
          padding: 5px 10px 5px 10px;

          .wcRight .wcHead {
            margin-bottom: 0;
          }
        }
      }

      .wcCustomizations {
        padding-left: 2%;
        padding-right: 2%;
      }

    }
  }

  .wcSidebar .wcColumn {
    > * {
      border-color: #e4e4e4;
    }

    >:first-child {
      border-top: none;
    }
    > * {
      border: none;
    }

    &.wcFocused>:first-child {
      border-top: none;
      > span {
        font-weight: bold;
      }
    }

    .wcHeader {
      height: 3em;
      color: #d7ebfb; background: #2167a9;
      border-top-left-radius: 8px;
      border-top-right-radius: 8px;

      span {
        color: #fff;
        font-size: 1.1em;
      }

      .wcButtons {
        .wcMinimize {
          background-image: url("../../gfx/themes/balloon/gfx/minus.svg");
        }
        .wcMaximize {
          background-image: url("../../gfx/themes/balloon/gfx/plus.svg");
        }
        .wcClose {
          background-image: url("../../gfx/themes/balloon/gfx/x_w.svg");
        }
      }
    }

    .wcContent {
      background-color: #f7f7f7;
      .wcChannel {
        .wcChannelIntro {
          background-color: transparent;
          padding-left: 0;
          padding-right: 0;
        }
      }
      &.wcBrowserContent .wcCustomizations {
        background-color: #3277bb;
        * {
          color: #fff;
        }
        a.wcCustomizeButton:before {
          background: url(../../gfx/themes/balloon/gfx/custom-white.svg) center center no-repeat;
        }
      }

      &.wcChannelContent {
        border-left: 1px solid #e4e4e4;
        border-right: 1px solid #e4e4e4;

        .wcChannelInput {
          .wcInputs {
            input, button, .wcInputButtons .wcInputButton {
              height: 40px;
              font-size: 15px;
            }
          }
        }
      }
    }

    .wcRecentTrigger {

      span {
        top: 10px;
      }
    }
  }

  // common:
  .wcCustomizations {
    a, a:hover {
      color: #686868; outline: none;
    }
    a.wcCustomizeButton:before {
      background: url(../../gfx/themes/balloon/gfx/custom.svg) center center no-repeat;
    }
  }

  .wcCounter {
    font-size: 10px;
  }

  .wcBrowser {
    * {
      color: #fff;
    }
    .wcChannels {
      .wcLabel {
        font-weight: bold;
      }
      .wcChannelTrigger {
        &.wcUnLockedChannel .wcDetails .wcName {
          background-image: url("../../gfx/themes/balloon/gfx/lock-semi.svg");
        }
        &.wcLockedChannel .wcDetails .wcName {
          background-image: url("../../gfx/themes/balloon/gfx/lock-full.svg");
        }

        &:hover {
          background-color: rgba(255,255,255,0.35);
          border-radius: 5px;
          .wcDetails {
            button.wcStreamStartButton.wcStreamVideo {
              background-image: url(../../gfx/icons/video-calls/video-white.svg);
            }
          }
        }

        .wcDetails {
          .wcCity, .wcCountry {
            color: rgba(235,235,235,0.65);
          }
        }
      }

      .wcLabel {
        font-size: 1.1em;
      }
    }

    background-color: #3277bb;

    .wcCounter {
      border: 0;
      border-top: 1px solid #69a3cb;
      color: #fff;
    }

    .wcFooter {
      border-top: none;
      padding: 5px 5px 5px 10px;

      .wcSearch {
        font-size: 20px;
        background-color: #d7ebfb;
        border-radius: 5px;
        background-position: 10px center;
        background-image: url("../../gfx/themes/balloon/gfx/search.svg");

        input {
          background-color: #d7ebfb;
          color: #00355f;
          margin-left: 30px;
          &:focus {
            padding-left: 2px;
          }
          &::placeholder {
            color: #00355f;
          }
        }
      }
    }

    &.wcBrowserRecent {
      .wcRecentChats .wcRecent {
        .wcRight .wcHead {
          .wcTime > span {
            color: #fff;
          }
        }

        &.wcUnread {
          background-color: rgba(0, 193, 121, 0.2);
        }
        &.wcUnread:hover {
          background-color: rgba(255,255,255,0.35);
          border-radius: 5px;
        }
        &.wcRead:hover {
          background-color: rgba(255,255,255,0.35);
          border-radius: 5px;
        }
      }
    }
  }

  .wcChannel  {
    .wcChannelIntro {
      border: none;
      border-bottom: 1px solid rgba(33, 103, 169, 0.2);
      padding: 10px;
      margin-top: 0;
      background-color: transparent;
      .wcAvatar {
        border-radius: 5px;
        max-height: 60px;
      }
    }

    .wcChannelData .wcMessages {
      color: #111;
      background: #f7f7f7;
      margin-bottom: 2%;

      .wcMessage {
        padding: 5px 2%;
        margin: 0;

        .wcRowHead {
          margin-bottom: -20px;
          margin-left: 45px;
        }

        .wcRowBody {
          .wcContent {
            background-color: #fff;
            box-shadow: 0 5px 10px rgba(0,0,0,0.02);
            border-radius: 15px;
            margin-left: -20px;
            margin-top: 20px;
            padding: 10px 16px;
            flex-grow: 0;

            .wcQuote {
              border-left-color: #2167a9;
              background-color: #f3f3f3;
              border-radius: 0 5px 0 0;

              .wcTime {
                margin-right: 5px;
              }

              .wcAvatar {
                border: none;
              }
            }
            .wcActionsButton {
              top: 50%;
              transform: translateY(-50%);
              border-radius: 5px;
              background: #efefef url(../../gfx/themes/balloon/gfx/3dots.svg) center center no-repeat;
              background-size: 14px;
              border: none;
              &:hover {
                border: 1px solid rgba(50, 119, 187, 0.32);
              }
            }
          }
          .wcAvatar {
            border: 4px solid #fff;
            border-bottom: 3px solid #fff;
            position: relative;
            z-index: 1;
          }
        }

        a {
          color: #1c7bff;
          text-decoration: none;
          &:hover {
	        text-decoration: underline;
          }
        }

        .wcUser {
          line-height: 22px;
          color: #011a4d;
          text-decoration: none;
          font-weight: bold;
        }

        .wcTime {
          &, & * {
            color: rgba(0,53,95,0.5); line-height: 22px;
          }
        }

        &.wcCurrentUser {
          .wcRowHead {
            margin-left: 0;
            margin-right: 45px;
            text-align: right;
            display: flex;
            flex-direction: row-reverse;
            .wcUser {
              margin-left: 5px;
              margin-right: 0;
            }
          }
          .wcRowBody {
            justify-content: flex-end;
            .wcContent {
              background-color: #afdbfc;
              box-shadow: 0 5px 10px rgba(0,0,0,0.04);
              order: 1;
              margin-left: 0;
              margin-right: -20px;
              a {
                color: #fff;
              }

              .wcQuote {
                background-color: #97cdf5;
                .wcUser {
                  color: #444;
                }
              }
            }
            .wcAvatar {
              order: 2;
            }
          }



        }

        .wcRowBody .wcContent {
          flex-grow: 0;
          min-width: auto;
          .wcInternalContent {
            display: inline;
          }
          &.wcEditing {
            .wcInternalContent, .wcTime {
              display: none;
            }
          }
          > .wcTime {
            margin-left: 10px;
            float: right;
          }
        }

        &.wcBatch {
          .wcRowHead {
            display: none;
          }
          .wcRowBody {
            > .wcAvatar {
              visibility: hidden;
            }
            .wcContent {
              //border-radius: 10px;
              margin-top: 0;
            }
          }
        }

      }
    }

    .wcCounter {
      padding: 0 2% 1% 2%;
    }

    .wcChannelInput {
      background-color: #f7f7f7;
      padding: 0 2% 2% 2%;

      .wcCurrentUserName {
        padding-bottom: 7px;
        font-weight: bold;
        color: #444;
      }

      .wcReplyTo {
        background-color: #ededed;
        .wcContent {
          background-color: #ededed;
        }
        .wcDeleteButton {
          background-image: url("../../gfx/themes/balloon/gfx/x.svg");
        }
      }

      .wcInputs {
        .wcInputButtons {
          .wcInputButton {
            border-radius: 0;
            border: none;
            box-shadow: none;
            height: 65px;
            display: inline-block;
            outline: none;

            -webkit-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
            -o-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
            transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);

            &:hover, &:hover > * {
              cursor: pointer;
            }

            &.wcImageAttachment {
              background: transparent url('../../gfx/themes/balloon/gfx/camera.svg') center center no-repeat;
            }

            &.wcFileAttachment {
              background: transparent url('../../gfx/themes/balloon/gfx/attach.svg') center center no-repeat;
              cursor: pointer;
            }

            &.wcEmoticon {
              background: transparent url('../../gfx/themes/balloon/gfx/emoticon.svg') center center no-repeat;
            }

            &.wcSoundAttachmentStart {
              background: transparent url(../../gfx/themes/balloon/gfx/mic.svg) center center no-repeat;
            }

            &.wcSoundAttachmentStop {
              background: transparent url(../../gfx/icons/stop-button.svg) center center no-repeat;
            }

            &:focus, &:active, &:hover {
              background-color: #eff3f6;
              border: none;
            }

          }

          .wcInputButtonSupplementary {
            background-color: transparent;
          }
        }

        .wcInput {
          font-size: 20px; padding: 15px 0 15px 20px; min-height: 65px; line-height: 31px; border-radius: 10px; background: #d7ebfb;
          &:focus {
              background: #FFF;
          }
          * {
            font-size: 20px; line-height: 31px;
          }
        }

      }

      .wcAttachments {
        .wcAttachment {
          .wcDelete {
            border-color: #b8bcc4;
            background: transparent url("../../gfx/themes/balloon/gfx/x.svg") center center no-repeat;
            &:hover {
              background-color: #e3e3e3;
            }
          }
        }
      }
    }
  }

  .wcRecentTrigger {
    &:hover {
      background-color: rgba(9, 193, 121, 0.15);
    }
    background-image: url("../../gfx/themes/balloon/gfx/recent-chats-inactive.svg");
    &.wcOpen {
      background-image: url("../../gfx/themes/balloon/gfx/recent-chats.svg");
    }

    span {
      font-size: 11px;
      font-weight: bold;
    }
  }
}

@mixin balloon-global {

  [role=tooltip].wcPopup-content.wcBalloonTheme-content,
  [role=dialog].wcPopup-content.wcBalloonTheme-content {
    @include balloon-common;

    border-color: #f3f3f3;
    box-shadow: 2px 2px 15px rgba(60,60,60,0.1);
    border-radius: 5px;
    overflow: hidden;

    > .wcBody {
      background-color: #f3f3f3 !important;
      padding-top: 20px;
      padding-bottom: 20px;
    }

    > .wcFooter {
      border: none;
      background: #f3f3f3 !important;
    }
    > .wcHeader {
      color: #d7ebfb;
      background: #2167a9;
      border: none;
      h5 {
        color: #fff;
        font-size: 1.1em;
        font-weight: bold;
      }
      .wcClose {
        background: transparent url("../../gfx/themes/balloon/gfx/x.svg") center center no-repeat;
        background-size: 100%;
      }
    }

    .wcRecentChats {
      .wcHeader {
        font-size: 1.1em;
        font-weight: bold;
        color: #444;
      }
      .wcRecent {
        .wcRight .wcHead {
          margin-bottom: 0;
          .wcName {
            color: #444;
          }
        }
      }
    }
  }

  .wcActionsPopup-content.wcBalloonTheme-content {
    .wcActions {
      background-color: #f7f7f7;

      .wcAction {
        border-color: #96a2af;

        &:hover {
          background-color: #e3e3e3;
        }
      }

      .wcApprove {
        background: transparent url("../../gfx/themes/balloon/gfx/approve.svg") no-repeat 50%;
      }

      .wcDelete {
        background: transparent url("../../gfx/themes/balloon/gfx/x.svg") no-repeat 50%;
      }

      .wcEdit {
        background: transparent url("../../gfx/themes/balloon/gfx/edit.svg") no-repeat 50%;
      }

      .wcMute {
        background: transparent url("../../gfx/themes/balloon/gfx/mute.svg") no-repeat 50%;
      }

      .wcBan {
        background: transparent url("../../gfx/themes/balloon/gfx/ban.svg") no-repeat 50%;
      }

      .wcSpam {
        background: transparent url("../../gfx/themes/balloon/gfx/spam.svg") no-repeat 50%;
      }

      .wcReply {
        background: transparent url("../../gfx/themes/balloon/gfx/reply.svg") no-repeat 50%;
      }
    }
  }

  .wcEmoticonsPopup-content.wcBalloonTheme-content {

    .wcAddonsLibrary .wcCategories .wcCategoryButton {

      &.wcEmoticon {
        background-image: url(../../gfx/themes/balloon/gfx/emoticon.svg);
      }
      &.wcGIF {
        background-image: url(../../gfx/themes/balloon/gfx/gif.svg);
      }
    }

    .wcCategory.wcCategoryGIFs .wcTenor .wcTenorSearch {
      background-image: url(../../gfx/themes/balloon/gfx/search.svg);
      border-radius: 5px;
      background-color: #d7ebfb;
      input {
        background-color: #d7ebfb;
        color: #00355f;
      }
    }
  }

  .wcInfoWindowPopup-content.wcBalloonTheme-content {
    background: #FFF !important;
    height: 120px;

    .wcContent .wcRight {
      .wcName {
        font-size: 1.3em;
        text-decoration: none;
      }
    }
  }
  .wcRecentEmpty-content.wcBalloonTheme-content {
    height: 100px;
    .wcRecentChats {
      display: flex;
      align-items: center;
    }
  }
}

@mixin balloon-common {
  --wc-font-color: #00355f;
  --wc-link-color: #6272d0;

  &, * {
    font-family: 'Rajdhani', sans-serif;
    font-size: 13px;
    line-height: 22px;
    color: var(--wc-font-color);
  }

  a {
    color: var(--wc-link-color);
    box-shadow: none;
  }

  button.wcButton, button.wcButton:focus, button.wcButton:active,
  input[type="button"].wcButton, input[type="button"].wcButton:focus, input[type="button"].wcButton:active,
  input[type="submit"].wcButton, input[type="submit"].wcButton:focus, input[type="submit"].wcButton:active {
    border-radius: 10px; background-image: none; background-repeat: no-repeat; border: 0!important;
	color: #FFF !important; padding: 5px 20px !important; box-shadow: none; font-weight: normal !important; line-height: 20px !important;
	height: 40px; outline: none; min-height: 40px !important;
	background: #084b80 !important;
	text-transform: none !important;

	-webkit-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
	-o-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
	transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);

    &:hover {
      background: #084b80 !important;
      box-shadow: none !important;
    }

    &.wcButtonDanger {
      color: #fff !important;
      background: #dc3545 !important;
      border-color: #dc3545 !important;
      &:hover {
        background: #ff0000 !important;
      }
    }
  }

  input[type="text"].wcControl, input[type="password"].wcControl, textarea.wcControl {
    height: 40px; font-size: 1em; line-height: 40px; box-shadow: none;
	background-color: #e0e0e0; border-radius: 5px; margin: 0; outline: 0 none; padding: 4px;
	padding-left: 15px; border: 1px solid transparent;
  }

  input[type="text"]:focus.wcControl, input[type="password"]:focus.wcControl, textarea:focus.wcControl {
    background-color: #e0e0e0; padding-left: 15px; border: 1px solid transparent;
  }

  ::placeholder {
    color: #00355f;
    font-family: 'Rajdhani', sans-serif;
  }

  input[type="button"].wcSubmit, button.wcSubmit {
    border-radius: 10px; text-transform: uppercase !important; font-weight: bold !important; font-size: 20px; height: 66px;
  }

  progress, progress[role][aria-valuenow] {
    background: #b8bcc4 !important;
  }

  progress::-webkit-progress-bar {
    background: #b8bcc4;
  }
  progress {
    color: #6272d0;
  }

  progress::-moz-progress-bar {
    background: #6272d0;
  }

  progress::-webkit-progress-value {
    background: #6272d0;
  }

  progress[aria-valuenow]:before  {
    background: #6272d0;
  }

  @media screen and (-webkit-min-device-pixel-ratio:0) {
    * {
      select,
      textarea,
      button,
      input {
        font-size: 16px;
      }
    }
  }

  .wcErrorBox {
    border-radius: 5px;
    background-color: #ffe9e9;
    border: none;
  }

}