@mixin clear {
  @include clear-common;

  .wcClassic {
    border: 0;
    box-shadow: 0 0 10px rgba(0,0,0,0.08);
    overflow: hidden;
    background: #f7f7f7;

    .wcTitle {
      padding: 15px; font-size: 150%; color: #FFF; background: #3b4653;
    }

    .wcLoadingContainer, .wcAuthContainer {
      padding: 0;
    }

    // desktop:
    &.wcDesktop {
      .wcBody {
        // messages area:
        .wcMessagesArea {
          .wcTabsContainer {
            background-color: #4d5a68;

            .wcTabs {
              .wcTab {

                &.wcCurrent {
                  border-bottom: 3px solid transparent;
                  background: #f7f7f7;

                  &:first-child {
                    border-top-left-radius: 0;
                  }
                  .wcName {
                    color: #151618;
                  }
                }

                .wcName {
                  color: #fff;
                  font-size: 16px;
                  padding-top: 4px;
                  padding-bottom: 4px;
                }
              }
            }

            .wcTabsNav .wcLeft {
              background-image: url("../../gfx/themes/clear/gfx/arrow-left.svg");
            }
            .wcTabsNav .wcRight {
              background-image: url("../../gfx/themes/clear/gfx/arrow-right.svg");
            }
          }

          .wcCustomizations {
            padding: 0 2% 2% 2%;
            background-color: #f7f7f7;
          }

          .wcGrid .wcGridChannelContainer {
            .wcGridChannel {
              border: none;

              .wcHeader {
                height: 3em;
                color: #FFF;
                background: #3b4653;
                border-color: #525c66;
                .wcName {
                  color: #fff;
                }
                .wcChannelClose:hover {
                  background-color: #525c66;
                }
              }
              .wcChannel {
                border: 1px solid #e4e4e4;
                border-top: none;
              }
            }

            &.wcFocused .wcGridChannel {
              border: none;
              box-shadow: 0 0 7px rgba(53, 123, 185, 0.79);
              .wcChannel {
                border-color: #fff;
                .wcInput {
                  border: 1px solid rgba(53, 123, 185, 0.17);
                }
              }
            }
          }
        }

        // browser area:
        &.wcBrowserAreaRight {
          .wcMessagesArea {
            padding-left: 0;
            padding-right: 0;
          }
        }

        &.wcBrowserAreaLeft {
          .wcMessagesArea {
            padding-left: 0;
            padding-right: 0;
          }
          .wcBrowserArea {
            padding-right: 2%;
            padding-left: 1%;
          }
        }

        .wcBrowserArea {
          background-color: #4d5a68;
          .wcIcon {
            background-color: #ffffff;
          }
        }
      }
    }
  }

  .wcMobile, .wcSidebar.wcMobile {
    .wcContent {
      padding-left: 0;
      padding-right: 0;
    }
    .wcTabs {
      background-color: #4d5a68;
      .wcTab {
        padding-bottom: 6px;
        padding-top: 6px;

        .wcName {
          font-size: 16px;
          color: #fff;
        }
        border-bottom: 0;
        &.wcCurrent {
          border-bottom: 0;
          background-color: #f9fafb;
          .wcName {
            color: #151618;
          }
        }

        .wcRedCounter {
          width: 22px;
          height: 22px;
          font-size: 1em;
        }
      }

      .wcTabCustomize {
        background-image: url(../../gfx/themes/clear/gfx/custom-gray.svg);
      }
    }
    &.wcSizeXXs {
      .wcTabs.wcTabsCompact {
       .wcTab {

          &.wcTabRecent {
            background-image: url(../../gfx/themes/clear/gfx/recent-chats-inactive.svg);
          }

          &.wcTabChats {
            background-image: url(../../gfx/themes/clear/gfx/chats.svg);
          }

          &.wcTabUsers {
            background-image: url(../../gfx/themes/clear/gfx/user.svg);
          }

          &.wcTabChannels {
            background-image: url(../../gfx/themes/clear/gfx/group.svg);
          }
       }
      }
    }

    .wcTabContent {
      .wcChannel {
        .wcChannelIntro {
          background-color: transparent;
          margin-top: 0;
          margin-bottom: 1%;
          padding: 0 0 2% 0;
        }
      }

      .wcChannelEntry {
        padding: 5px 10px 5px 10px;

        &:focus, &:active, &:hover {
          background-color: rgba(255,255,255,0.35);
        }

        &.wcUnLockedChannel .wcName {
          background-image: url("../../gfx/themes/clear/gfx/lock-semi.svg");
        }
        &.wcLockedChannel .wcName {
          background-image: url("../../gfx/themes/clear/gfx/lock-full.svg");
        }
      }

      .wcBrowser {
        padding-top: 0;
        background-color: #f7f7f7;

        .wcChannels {
          .wcChannelTrigger {
            .wcName {
              color: #151618;
            }
            &:hover {
              background-color: rgba(255,255,255,0.35);
              .wcDetails {
                button.wcStreamStartButton.wcStreamVideo {
                  background-image: url(../../gfx/icons/video-calls/video.svg);
                }
              }
            }
          }
        }
      }

      .wcRecentChats {
        .wcHeader {
          font-size: 1.1em;
          color: #444;
          padding-left: 10px
        }

        .wcRecent {
          padding: 5px 10px 5px 10px;

          .wcRight .wcHead {
            margin-bottom: 0;
          }
        }
      }

      .wcCustomizations {
        padding-left: 2%;
        padding-right: 2%;
      }

    }
  }

  .wcSidebar .wcColumn {
    > * {
      border-color: #e4e4e4;
    }

    >:first-child {
      border-top: none;
    }
    > * {
      border: none;
    }

    &.wcFocused>:first-child {
      border-top: none;
      > span {
        font-weight: bold;
      }
    }

    .wcHeader {
      height: 3em;
      color: #FFF;
      background: #3b4653;

      span {
        color: #fff;
        font-size: 1.1em;
      }

      .wcButtons {
        .wcMinimize {
          background-image: url("../../gfx/themes/clear/gfx/minus.svg");
        }
        .wcMaximize {
          background-image: url("../../gfx/themes/clear/gfx/plus.svg");
        }
        .wcClose {
          background-image: url("../../gfx/themes/clear/gfx/x.svg");
        }
      }
    }

    .wcContent {
      background-color: #f7f7f7;
      &.wcBrowserContent .wcCustomizations {
        background-color: #4d5a68;
        * {
          color: #fff;
        }
        a.wcCustomizeButton:before {
          background: url(../../gfx/themes/clear/gfx/custom-gray.svg) center center no-repeat;
        }
      }

      .wcChannel {
        .wcChannelIntro {
          background-color: transparent;
          padding-left: 0;
          padding-right: 0;
        }
      }

      &.wcChannelContent {
        border-left: 1px solid #e4e4e4;
        border-right: 1px solid #e4e4e4;

        .wcChannelInput {
          .wcInputs {
            input, button, .wcInputButtons .wcInputButton {
              height: 40px;
              font-size: 15px;
            }
          }
        }
      }
    }

    .wcRecentTrigger {

      span {
        top: 10px;
      }
    }
  }

  // common:
  .wcCustomizations {
    a, a:hover {
      color: #686868; outline: none;
    }
    a.wcCustomizeButton:before {
      background: url(../../gfx/themes/clear/gfx/custom.svg) center center no-repeat;
    }
  }

  .wcCounter {
    font-size: 10px;
  }

  .wcBrowser {
    * {
      color: #fff;
    }
    .wcChannels {
      .wcLabel {
        font-weight: bold;
      }
      .wcChannelTrigger {
        &.wcUnLockedChannel .wcDetails .wcName {
          background-image: url("../../gfx/themes/clear/gfx/lock-semi.svg");
        }
        &.wcLockedChannel .wcDetails .wcName {
          background-image: url("../../gfx/themes/clear/gfx/lock-full.svg");
        }

        &:hover {
          background-color: rgba(255,255,255,0.35);
          border-radius: 5px;

          .wcDetails {
            button.wcStreamStartButton.wcStreamVideo {
              background-image: url(../../gfx/icons/video-calls/video-white.svg);
            }
          }
        }
      }

      .wcLabel {
        font-size: 1.1em;
      }
    }

    background-color: #4d5a68;

    .wcCounter {
      border: 0;
      border-top: 1px solid #62717f;
      color: #fff;
    }

    .wcFooter {
      border-top: none;
      padding: 5px 5px 5px 10px;

      .wcSearch {
        color: #fff; font-size: 20px;
        background-color: #62717f;
        border-radius: 0;
        background-position: 10px center;
        background-image: url("../../gfx/themes/clear/gfx/search.svg");

        input {
          background-color: #62717f;
          color: #fff;
          margin-left: 30px;
          &:focus {
            padding-left: 2px;
          }
          &::placeholder {
            color: #fff;
          }
        }
      }
    }

    &.wcBrowserRecent {
      .wcRecentChats .wcRecent {
        &.wcRead:hover {
          background-color: rgba(255,255,255,0.35);
          border-radius: 5px;
        }
      }
    }
  }

  .wcChannel {
    .wcChannelIntro {
      border: none;
      padding: 10px;
      margin-top: 0;
      margin-bottom: 0;
      .wcAvatar {
        border-radius: 5px;
        max-height: 60px;
      }
    }

    .wcChannelData .wcMessages {
      color: #111;
      background: #f7f7f7;
      margin-bottom: 2%;

      .wcMessage {
        padding: 5px 2%;
        border-bottom: 1px solid #ededed;
        margin: 0;

        .wcRowBody .wcContent {
          background-color: transparent;
        }

        a {
          color: #00d5ff;
          text-decoration: none;
          &:hover {
	        text-decoration: underline;
          }
        }

        .wcUser {
          line-height: 22px;
          color: #3b4653;
          text-decoration: none;
          font-weight: bold;
        }

        .wcTime {
          &, & * {
            color: #96a2af; line-height: 22px;
          }
        }

        .wcRowBody .wcContent .wcQuote {
          border-left-color: #525252;
          background-color: #f3f3f3;
          border-radius: 0 5px 0 0;
        }

        &.wcCurrentUser {
          background: #f3f3f3;

          .wcRowBody .wcContent .wcQuote {
            border-radius: 0 5px 0 0;
            background-color: #ededed;
          }
          .wcRowHead .wcUser {
            color: #444;
          }
        }
      }
    }

    .wcCounter {
      padding: 0 2% 1% 2%;
    }

    .wcChannelInput {
      background-color: #f7f7f7;
      padding: 0 2% 2% 2%;

      .wcCurrentUserName {
        padding-bottom: 7px;
        font-weight: bold;
        color: #444;
      }

      .wcReplyTo {
        background-color: #ededed;
        .wcContent {
          background-color: #ededed;
        }
        .wcDeleteButton {
          background-image: url("../../gfx/themes/clear/gfx/x.svg");
        }
      }

      .wcInputs {
        .wcInputButtons {
          .wcInputButton {
            border-radius: 0;
            border: none;
            box-shadow: none;
            height: 65px;
            display: inline-block;
            outline: none;

            -webkit-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
            -o-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
            transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);

            &:hover, &:hover > * {
              cursor: pointer;
            }

            &.wcImageAttachment {
              background: transparent url('../../gfx/themes/clear/gfx/camera.svg') center center no-repeat;
            }

            &.wcFileAttachment {
              background: transparent url('../../gfx/themes/clear/gfx/attach.svg') center center no-repeat;
              cursor: pointer;
            }

            &.wcEmoticon {
              background: transparent url('../../gfx/themes/clear/gfx/emoticon.svg') center center no-repeat;
            }

            &.wcSoundAttachmentStart {
              background: transparent url(../../gfx/themes/clear/gfx/mic.svg) center center no-repeat;
            }

            &.wcSoundAttachmentStop {
              background: transparent url(../../gfx/icons/stop-button.svg) center center no-repeat;
            }

            &:focus, &:active, &:hover {
              background-color: #eff3f6;
              border: none;
            }

          }

          .wcInputButtonSupplementary {
            background-color: transparent;
          }
        }

        div.wcInput {
          border-radius: 0; font-size: 20px; padding: 16px 0 16px 20px; min-height: 65px; line-height: 31px; background: #eff3f6;
          &:focus {
              background: #e8eef2;
          }
          * {
            font-size: 20px; line-height: 31px;
          }
        }

      }

      .wcAttachments {
        .wcAttachment {
          .wcDelete {
            border-color: #525252;
            background: transparent url("../../gfx/themes/clear/gfx/x.svg") center center no-repeat;
            &:hover {
              background-color: #e3e3e3;
            }
          }
        }
      }
    }
  }

  .wcRecentTrigger {
    &:hover {
      background-color: rgba(9, 193, 121, 0.15);
    }
    background-image: url("../../gfx/themes/clear/gfx/recent-chats-inactive.svg");
    &.wcOpen {
      background-image: url("../../gfx/themes/clear/gfx/recent-chats.svg");
    }

    span {
      font-size: 11px;
      font-weight: bold;
    }
  }
}

@mixin clear-global {

  [role=tooltip].wcPopup-content.wcClearTheme-content,
  [role=dialog].wcPopup-content.wcClearTheme-content {
    @include clear-common;

    border-color: #e9eef0;
    box-shadow: 2px 2px 15px rgba(60,60,60,0.1);
    border-radius: 5px;
    overflow: hidden;

    > .wcBody {
      background-color: #f3f3f3 !important;
      padding-top: 20px;
      padding-bottom: 20px;
    }

    > .wcHeader, > .wcFooter {
      background: #3b4653 !important;
      border: none;
    }
    > .wcHeader {
      color: #fff;
      h5 {
        color: #fff;
        font-size: 1.1em;
        font-weight: bold;
      }
      .wcClose {
        background: transparent url("../../gfx/themes/clear/gfx/x.svg") center center no-repeat;
        background-size: 100%;
      }
    }

    .wcRecentChats {
      .wcHeader {
        font-size: 1.1em;
        font-weight: bold;
        color: #444;
      }
      .wcRecent {
        .wcRight .wcHead {
          margin-bottom: 0;
          .wcName {
            color: #444;
          }
        }
      }
    }
  }

  .wcActionsPopup-content.wcClearTheme-content {
    .wcActions {
      .wcAction {
        border-color: #96a2af;

        &:hover {
          background-color: #eff3f6;
        }
      }

      .wcApprove {
        background: transparent url("../../gfx/themes/clear/gfx/approve.svg") no-repeat 50%;
      }

      .wcDelete {
        background: transparent url("../../gfx/themes/clear/gfx/x.svg") no-repeat 50%;
      }

      .wcEdit {
        background: transparent url("../../gfx/themes/clear/gfx/edit.svg") no-repeat 50%;
      }

      .wcMute {
        background: transparent url("../../gfx/themes/clear/gfx/mute.svg") no-repeat 50%;
      }

      .wcBan {
        background: transparent url("../../gfx/themes/clear/gfx/ban.svg") no-repeat 50%;
      }

      .wcSpam {
        background: transparent url("../../gfx/themes/clear/gfx/spam.svg") no-repeat 50%;
      }

      .wcReply {
        background: transparent url("../../gfx/themes/clear/gfx/reply.svg") no-repeat 50%;
      }
    }
  }

  .wcEmoticonsPopup-content.wcClearTheme-content {
    .wcCategory.wcCategoryGIFs .wcTenor .wcTenorSearch {
      input {
        background-color: #ffffff;
      }
    }
  }

  .wcInfoWindowPopup-content.wcClearTheme-content {
    background: #FFF !important;
    height: 120px;

    .wcContent .wcRight {
      .wcName {
        font-size: 1.3em;
        text-decoration: none;
      }
    }
  }
  .wcRecentEmpty-content.wcClearTheme-content {
    height: 100px;
    .wcRecentChats {
      display: flex;
      align-items: center;
    }
  }
}

@mixin clear-common {
  --wc-font-color: #151618;
  --wc-link-color: #00d5ff;

  &, * {
    font-family: 'Rajdhani', sans-serif;
    font-size: 13px;
    line-height: 22px;
    color: var(--wc-font-color);
  }

  a {
    color: var(--wc-link-color);
    box-shadow: none;
  }

  button.wcButton, button.wcButton:focus, button.wcButton:active,
  input[type="button"].wcButton, input[type="button"].wcButton:focus, input[type="button"].wcButton:active,
  input[type="submit"].wcButton, input[type="submit"].wcButton:focus, input[type="submit"].wcButton:active {
    border-radius: 0; background-image: none; background-repeat: no-repeat; border: 0!important;
	color: #FFF !important; padding: 5px 20px !important; box-shadow: none; font-weight: normal !important; line-height: 20px !important;
	height: 40px; outline: none; min-height: 40px !important;
	background: #357bb9 !important;
	text-transform: none !important;

	-webkit-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
	-o-transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);
	transition: all 250ms cubic-bezier(0.62, 0.155, 0.47, 0.86);

    &:hover {
      background: #296ca7 !important;
	  box-shadow: none !important; font-weight: normal !important;
    }

    &.wcButtonDanger {
      color: #fff !important;
      background: #dc3545 !important;
      border-color: #dc3545 !important;
      &:hover {
        background: #ff0000 !important;
      }
    }
  }

  input[type="text"].wcControl, input[type="password"].wcControl, textarea.wcControl {
    height: 40px; font-size: 1em; line-height: 40px; box-shadow: none;
	background-color: #eff3f6; border-radius: 0; margin: 0; outline: 0 none; padding: 4px;
	padding-left: 15px; border: 1px solid transparent;
  }

  input[type="text"]:focus.wcControl, input[type="password"]:focus.wcControl, textarea:focus.wcControl {
    background-color: #e8eef2; padding-left: 15px; border: 1px solid transparent;
  }

  ::placeholder {
    color: #686868;
    font-family: 'Rajdhani', sans-serif;
  }

  input[type="button"].wcSubmit, button.wcSubmit, button.wcSubmit:hover, button.wcSubmit:active {
    border-radius:0;  text-transform: uppercase !important; font-weight: bold !important; font-size: 20px; padding: 11px 20px !important; height: 66px; margin-top: -1px;
  }

  progress, progress[role][aria-valuenow] {
    background: #b8bcc4 !important;
  }

  progress::-webkit-progress-bar {
    background: #b8bcc4;
  }

  progress {
    color: #00d5ff;
  }

  progress::-moz-progress-bar {
    background: #00d5ff;
  }

  progress::-webkit-progress-value {
    background: #00d5ff;
  }

  progress[aria-valuenow]:before  {
    background: #00d5ff;
  }


  @media screen and (-webkit-min-device-pixel-ratio:0) {
    * {
      select,
      textarea,
      button,
      input {
        font-size: 16px;
      }
    }
  }

  .wcErrorBox {
    border-radius: 5px;
    background-color: #ffe9e9;
    border: none;
  }

}